import { YYTerminalCheck } from '../YYUtils/YYUtils';

let win = window;
win.__jsBridge = {
    callBackPool:{},
    ack_no:1
};
window.getOpenWindowClassName = function() {return {className_ios:"ZYBBaseWebViewController"}}

win.__jsBridge.invoke = function (_action, _data, _callBack) {
    

    var fullParam = {
        action:_action,
        businessData:"ZYBHybirdViewController"
    };
    if (_data) {
        fullParam.param = _data;
    }


    if (_callBack) {
        var rndkey = 'cbk' + win.__jsBridge.ack_no++;
        fullParam.callbackKey = rndkey;
        win.__jsBridge.callBackPool[rndkey] = _callBack;
    }
    let message = win.__jsBridge.param(fullParam);

    let inApp = YYTerminalCheck.isInApp();
    if (inApp) {
        console.log("App 内应用");
    }else{
        console.log("浏览器 内应用");
    }
    if (win.ZYBJSBridge) {
        console.log("发送消息AAA:" + message);
        win.ZYBJSBridge.postMessage(message);
    }else{
        if (inApp) {
            if (YYTerminalCheck.isWindows()) {
                console.log("谷歌开发者模式iPhone应用");
            }else{
                
                console.log("发送消息:" + message);
                win.webkit.messageHandlers.ZYBJSBridge.postMessage(message); 
            }
        } else {
            console.log("不在App内");
        }
    }
}



win.__jsBridge.callback = function(_data){
    var callBackKey = _data.callbackKey;
    var func = win.__jsBridge.callBackPool[callBackKey];

    var data = _data.data;
    if (typeof func == 'function') {
        //回调参数里面删除callBack相关
        delete _data.callBackKey;
        func(data);
        delete win.__jsBridge.callBackPool.callBackKey;
    }
}
/**
 * 参数转json字符串
 * @param {*} _data 
 */
win.__jsBridge.param= function(_data){
    let string = JSON.stringify(_data);
    let object = string? string:'';
    return object
}


const userAgent = navigator.userAgent.toLocaleLowerCase();
// 浏览器类型
const IPHONE = 'iphone';
const IPAD = 'ipad';
const ANDROID = 'android';
const WINDOWS = 'windows';
const browser = {
    android: userAgent.indexOf(ANDROID) > -1 || userAgent.indexOf('adr') > -1,
    iPhone: userAgent.indexOf(IPHONE) > -1,
    iPad: userAgent.indexOf(IPAD) > -1,
    windows: userAgent.indexOf(WINDOWS) > -1
}

export var checkSystem = {
    isAndroid : function () {
        return browser.android
    },
    isiPhone : function () {
        return browser.iPhone
    }
}

export function yy_hybrid(action, params, callBack) {
    let androidVC = 10;
     if (androidVC< 4.2) {
        //  iframeHybrid(action,params,callBack);
     }else{
        

        if (params && Object.prototype.toString.call(params) == '[object Function]') {
            alert("aaaaa");
            callBack = params;
            params={};
        }

        if (checkSystem.isiPhone) {
            win.__jsBridge.invoke(action,params,callBack);
        }
     }
    
}
