<template>
  <div class="body_context">
    <button @click="hy_createSocket">开启Socket</button>
    <button @click="hy_closeSocket">关闭Socket</button>
    <button @click="hy_sendWSPush">发送消息给NA</button>
    <button @click="hy_getWSPush">接收NA消息</button>
    <p class="msg_context"> aaa </p>
  </div>
</template>

<script>
import { createSocket } from "@sven0706/websocket";
import { sendWSPush } from "@sven0706/websocket";
import {yy_hybrid} from '../YYConfig/YYJSbridge'

// 接收消息
const getsocketData = (e) => {
  // 创建接收消息函数
  const data = e && e.detail.data;
  console.log(data);
};
window.addEventListener("onmessageWS", getsocketData);

export default {
  name: "YYWebsocket",
  methods: {
    hy_createSocket() {
      window.addEventListener("onmessageWS", getsocketData);
      var pageUrl = "ws://10.250.39.116:8080";
      createSocket(pageUrl);
      setTimeout(() => {
          document.getElementsByClassName("msg_context").innerHTML = "创建socket 连接"
          yy_hybrid("opensocket",
           {"url":pageUrl}
          );
      }, 3000);
      
    },

    hy_closeSocket() {
      var pageUrl = "ws://10.250.39.116:8080";
      yy_hybrid("closesocket",
        {"url":pageUrl}
      );
      window.removeEventListener("onmessageWS", getsocketData);

    },
    hy_sendWSPush() {
      let data = '{"name":"hybrid", "version":"1.4.0"}';
      sendWSPush(data);
    },

    hy_getWSPush() {
      yy_hybrid("sendMsgToJs",{}
      );
    },
  },
  beforeUnmount () {
    this.hy_closeSocket()
  }
};
</script>
<style src="./YYWebsocket.css" scoped></style>
