const ua = navigator.userAgent;
console.log("当前UA(全小写): "+ua.toLowerCase());
const iPhone = "iphone";
const Android = "android";
// const Windows = "windows";
const iPad = "ipad";

const brower = {
    android: ua.toLowerCase().indexOf(Android) != -1 || ua.toLowerCase().indexOf('adr') != -1,
    iPhone : ua.toLowerCase().indexOf(iPhone) != -1,
    iPad: ua.toLowerCase().indexOf(iPad) != -1,
    Windows: ua.toLowerCase().indexOf('safari') != -1

}

const YYTerminalCheck = {
    isInApp:function (){
        if (brower.android || brower.iPhone || brower.iPad) {
            return true;
        } else {
            return false;
        }
    },
    isAndroid:function () {
        return brower.android;
    },
    isiPone:function(){
        return brower.iPhone;
    },
    isiPad:function(){
        return brower.iPhone;
    },
    isWindows:function(){
        return brower.Windows;
    }

};
export {YYTerminalCheck};
export default YYTerminalCheck;
